import React, { useState, useEffect }  from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { ButtonGroup, Grid, TextField } from '@mui/material';
import { styled } from '@mui/material/styles';
import { purple } from '@mui/material/colors';
import EditIcon from '@mui/icons-material/Edit';

function CardInfo (props) {

    const [product, setProduct] = useState({});
    const [styleTitle, setStyleTitle] = useState({fontSize: props.fontTitle});
    useEffect(() => {
        setProduct(props.product);
        setStyleTitle({
            fontSize: props.fontTitle
        })
    }, []);
    useEffect(() => {
        setStyleTitle({fontSize: props.fontTitle});
    }, [ props.fontTitle ]);
    
    const handleEdit = (id) => {
        props.stateEdit(id)
    }

    const editTitle = (data) => {
        setProduct({...product, name: data.target.value});
        data.preventDefault();
        
    }
    const ColorButton = styled(Button)(({ theme }) => ({
        color: theme.palette.getContrastText(purple[500]),
        backgroundColor: purple[500],
        '&:hover': {
          backgroundColor: purple[700],
        },
    }));

    const handleQuantity = (data) => {
        props.updateTotal({value: data.target.value, index: props.product.id});
    }

    return (
        <Card>
            <CardMedia
                component="img"
                alt="green iguana"
                height="140"
                image={product.image}
            />
            <CardContent>
                <div style={styleTitle}>
                    {product.name}
                </div>
                <EditIcon onClick={() => handleEdit(product.id)}></EditIcon>
            
                { props.editState === product.id ? <TextField defaultValue={product.name} onChange={editTitle}></TextField>
                    :<></>
                }
                <Grid container>
                    <Grid item xs={4}>
                        <Typography>
                            ${product.price}
                        </Typography>
                    </Grid>
                    <Grid item xs={4}>
                        <TextField size='small' type="number" defaultValue={product.quantity} id="outlined-basic" variant="outlined" 
                        onChange={handleQuantity} />
                    </Grid>
                </Grid>
                <Typography variant="body2" color="text.secondary">
                    {product.description}
                </Typography>
            </CardContent>
            <CardActions>
                <ButtonGroup variant='text'>
                    <ColorButton variant="contained">Add to cart</ColorButton>
                    <Button size="small">Learn More</Button>
                </ButtonGroup>
            </CardActions>
        </Card>
    )
}

export default CardInfo;